import { useEffect, useState } from "react";
import styled from "styled-components";

//Styles
const StyledChecklistItem = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 15px 15px 15px 10px;
  background-color: ${(props) => (props.isActive ? "green" : "lightblue")};
  color: ${(props) => (props.isActive ? "white" : "black")};
  cursor: pointer;
`;

const StyledEmoji = styled.span`
  padding-left: 5px;
`;

const StyledCaption = styled.p`
  margin: 0;
  padding-left: 5px;
`;

const ChecklistItem = ({ action, emoji, setActiveItemsCount }) => {
  //State
  const [isActive, setIsActive] = useState(false);

  //Event Handlers
  const changeHandler = () => {
    setIsActive(!isActive);
  };

  useEffect(() => {
    if (!isActive) {
      setActiveItemsCount((prevCount) => {
        if (prevCount !== 0) {
          return prevCount - 1;
        }
        return prevCount;
      });
    }
    if (isActive) {
      setActiveItemsCount((prevCount) => prevCount + 1);
    }
  }, [isActive, setActiveItemsCount]);

  return (
    <StyledChecklistItem isActive={isActive} onClick={changeHandler}>
      <input type="checkbox" checked={isActive} onChange={changeHandler} />
      <StyledEmoji role="img">{emoji}</StyledEmoji>
      <StyledCaption>{action}</StyledCaption>
    </StyledChecklistItem>
  );
};

export default ChecklistItem;
